import React, { Component } from "react";
import CreateTodos from "../ToDoUI/CreateTodos";
import { v4 as uuidv4 } from "uuid";
import TodoItem from "./Components/TodoItem";
import FiltredTodos from "../ToDoUI/FiltredTodos";

export default class ToDo extends Component {
  state = {
    todos: [],
    todosText: "",
    searchItemText: "",
  };

  //input search < FIltredTodos.js
  searchTodosHandler = (event) => {
    this.setState({ searchItemText: event.target.value });
  };

  //btn add < CreateTodos.js
  addTodosHandler = () => {
    let { todosText, todos } = this.state;

    if (todosText !== "") {
      let task = { id: uuidv4(), text: todosText, completed: false };
      this.setState({ todos: [...todos, task] });
    }
    this.setState({ todosText: "" });
  };

  // input text < CreateTodos.js
  addTextTodosHandler = (event) => {
    this.setState({ todosText: event.target.value });
  };

  // checkbox toggle < TodoItem.js
  toggleCheckboxToDosHandler = (taskID) => {
    let { todos } = this.state;
    this.setState({
      todos: todos.map((todo) =>
        todo.id === taskID ? { ...todo, completed: !todo.completed } : todo
      ),
    });
  };

  //btn delete < TodoItem.js
  deleteTodosHandler = (taskID) => {
    let { todos } = this.state;
    this.setState({ todos: todos.filter((item) => item.id !== taskID) });
  };

  //input text < TodoItem.js
  changeCurrentTextHandler = (taskID, event) => {
    let { todos } = this.state;
    this.setState({
      todos: todos.map((todo) =>
        todo.id === taskID ? { ...todo, text: event.target.value } : todo
      ),
    });
  };

  selectFiltredTodos = () => {
    let { todos, searchItemText } = this.state;
    return todos.filter((todo) => todo.text.includes(searchItemText));
  };

  render() {
    let { todos } = this.state;

    let filtredTodos = this.selectFiltredTodos();
    return (
      <div className="wrapperTodos">
        <CreateTodos
          onClickAddTodosHandler={this.addTodosHandler}
          onAddTextTodosHandler={this.addTextTodosHandler}
          valueInputText={this.state.todosText}
        />

        {todos.length > 0 && (
          <FiltredTodos onSearchTodosHandler={this.searchTodosHandler} />
        )}

        {filtredTodos.length > 0 &&
          filtredTodos.map(({ id, text, completed }) => (
            <TodoItem
              key={id}
              id={id}
              text={text}
              completed={completed}
              onToggleCheckboxToDosHandler={this.toggleCheckboxToDosHandler}
              onDeleteTodosHandler={this.deleteTodosHandler}
              onChangeCurrentTextHandler={this.changeCurrentTextHandler}
            />
          ))}
      </div>
    );
  }
}
