import React from "react";
import PropTypes from "prop-types";

const CreateTodos = ({
  onClickAddTodosHandler,
  onAddTextTodosHandler,
  valueInputText,
}) => {
  return (
    <>
      <div className="createTaskWrapper">
        <label htmlFor="" className="labelInputCreateToDoWrapper">
          text todo:
          <input
            type="text"
            className="createTaskInput"
            value={valueInputText}
            onChange={onAddTextTodosHandler}
          />
        </label>

        <button
          className="js-btn-addToDo"
          disabled={!valueInputText}
          onClick={onClickAddTodosHandler}
        >
          add
        </button>
      </div>
    </>
  );
};

CreateTodos.propTypes = {
  onClickAddTodosHandler: PropTypes.func.isRequired,
  onAddTextTodosHandler: PropTypes.func.isRequired,
  valueInputText: PropTypes.string,
};

export default CreateTodos;
