import React from "react";
import PropTypes from "prop-types";

const FiltredTodos = ({ onSearchTodosHandler, searchText }) => {
  return (
    <div>
      <label className="labelSearch">
        text to search:
        <input
          type="text"
          className="filterInputInfo"
          value={searchText}
          onChange={onSearchTodosHandler}
        />
      </label>
    </div>
  );
};

FiltredTodos.propTypes = {
  onSearchTodosHandler: PropTypes.func.isRequired,
  searchText: PropTypes.string,
};

export default FiltredTodos;
