import React from "react";
import PropTypes from "prop-types";

const TodoItem = ({
  id,
  text,
  completed,
  onToggleCheckboxToDosHandler,
  onDeleteTodosHandler,
  onChangeCurrentTextHandler,
}) => {
  return (
    <>
      <div className="itemToDos">
        <input
          type="checkbox"
          checked={completed}
          onChange={() => onToggleCheckboxToDosHandler(id)}
          className="checkboxToDo"
        />

        <input
          type="text"
          disabled={completed}
          className={completed ? "inputTask active" : "inputTask"}
          value={text}
          onChange={(event) => onChangeCurrentTextHandler(id, event)}
        />

        <button
          className="js-btn-delete"
          onClick={() => onDeleteTodosHandler(id)}
        >
          &#10006;
        </button>
      </div>
    </>
  );
};

TodoItem.propTypes = {
  id: PropTypes.string.isRequired,
  text: PropTypes.string.isRequired,
  completed: PropTypes.bool.isRequired,
  onToggleCheckboxToDosHandler: PropTypes.func.isRequired,
  onDeleteTodosHandler: PropTypes.func.isRequired,
  onChangeCurrentTextHandler: PropTypes.func.isRequired,
};

export default TodoItem;
